import React from "react";
import {NavLink} from 'react-router-dom';
const Navbar=()=>{
    return (
        <div className='container-fluid nav_bg '>

        <div className='row'>
            <div className="col-10 mx-auto">
                <nav className="navbar navbar-expand-lg navbar-light">
        
                    <NavLink exact className="navbar-brand text-capitalize text display-1"  to="/">Mobile Guru</NavLink>

                    <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                        <span className="navbar-toggler-icon"></span>
                    </button>

                    <div className="collapse navbar-collapse" id="navbarSupportedContent">
                        <ul className="navbar-nav ml-auto ">
                            <li className="nav-item active">
                                <NavLink exact className="nav-link text" activeClassName="menu_active" to="/">Home <span className="sr-only">(current)</span></NavLink>
                            </li>
                            <li className="nav-item">
                                <NavLink exact className="nav-link text" activeClassName="menu_active" to="/service">Service</NavLink>
                            </li>
                            <li className="nav-item">
                                <NavLink exact className="nav-link text" activeClassName="menu_active" to="/about">About</NavLink>
                            </li>
                            <li className="nav-item">
                                <NavLink exact className="nav-link text" activeClassName="menu_active" to="/contactus">ContactUs</NavLink>
                            </li>
                        </ul>
                    
                    </div>
                </nav>

            </div>
        </div>
            
            
            
        </div>
    );   
}

export default Navbar;


